@import url(https://fonts.googleapis.com/css2?family=Lato:wght@700&family=Lora&display=swap);
body, p {
  margin: 0;
  font-family: 'Lora', serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h1, h2, h3, h4, h5, h6 {
  font-family: 'Lato', sans-serif;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App-header {
  background-color: #000e1f;
  min-height: 400vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 3vmin);
  font-family: 'Lora', serif;
  color: white;
  text-align: center;
}

.content {
  text-align: center;
  display: flex;
  flex-direction: column;
}

div:empty {
  padding-top:50px;
  height:0;
}

.App {
  width: 100%;
}

.navbar {
  background-color: #000e1f;
  border-radius: 0px;
}

.header-img {
  height: auto;
  width: 50px;
}
.header {
  padding: 0% !important;
  margin: 0%;
  font-family: 'Lato', sans-serif;
}

.navbarFloat{
  box-shadow: 0px 1px 10px #999;
}

.footer {
  background-color: #000e1f;
  color: aliceblue;
  text-align: center;
}
.credits{
  color: #b8b8b8;
}
.intro {
  background-color: #000e1f;
  text-align: center;
  justify-content: center;
  width:100%;
  margin:0;
}
.intro-image {
  height: 100%;
  max-height: 550px;
  width: 100%;
  justify-content: center;
  text-align: center;
}
.intro-text {
  color: ghostwhite;
  font-size: 6vw;
  text-align: center;
  justify-content: center;
  padding-top: 12vw;
  padding-bottom: 5vw;
  text-align: center;
}
.aboutUs {
  background-color: #e1eff2;
  text-align: center;
}
.blank {
  height: 6vw;
  background-color: white;
}
.aboutUsCard {
  background-color: #FFFFFF;
}
.people {
  background-color: #FFFFFF;
  color: #000000;
}
.card {
  background-color: #FFFFFF;
  margin: auto;
}

